.App {
  text-align: center;
  background-image: url("./BgImage.svg");
  background-size: cover;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  overflow: auto;

  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.App-logo {
  height: 100%;
  margin-top: 3rem;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-scale infinite 5s cubic-bezier(1, 0, 0, 2) alternate-reverse;
  }
}

.fontStyle1 {
  font-family: "Aguafina Script";
  margin-top: 1rem;
  font-size: 70px;
  margin-bottom: none;
}

.EmailContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.ContainerStyle {
width: 280px;
height: 48px;
display: flex;
border: none;
margin-right: 1rem;
padding-left: 1rem;
border-radius: 6px;
border-color: #FFFFFF;
}

.Contactform {
display:flex;
flex-direction:column;
margin-bottom: 2rem;
align-items: flex-start;
row-gap: 0.25rem;

}

.ButtonStyle {
  width: 99px;
  height: 48px;
  border: 1px solid #FFFFFF;  
  border-radius: 6px;
  color: #FFFFFF;
  background: transparent;
}


@keyframes App-logo-scale {
  from {
    transform: scale(50%);

  }
}




.Contact {
  height: 50;
  margin-bottom: 1rem;
  }